"use client";

import { useEffect, useState } from "react";
import { createClient } from "../../../lib/supabase/supabaseClient";
import { LineChart, Line, PieChart, Pie, Cell, Tooltip, XAxis, YAxis, ResponsiveContainer } from "recharts";
import { motion } from "framer-motion";

export default function AnalyticsPage() {
  const supabase = createClient();
  const [roiData, setRoiData] = useState<any[]>([]);
  const [allocData, setAllocData] = useState<any[]>([]);
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    const fetchAnalytics = async () => {
      const {
        data: { user },
      } = await supabase.auth.getUser();
      if (!user) return;

      // Fetch ROI trend (mocked from holdings table)
      const { data } = await supabase
        .from("roi_history")
        .select("date, roi_percentage, asset_allocation");

      if (data) {
        setRoiData(data.map((d) => ({ date: d.date, roi: d.roi_percentage })));

        const alloc = data[data.length - 1]?.asset_allocation || {};
        const formattedAlloc = Object.entries(alloc).map(([k, v]) => ({
          name: k,
          value: v,
        }));
        setAllocData(formattedAlloc);
      }
      setLoading(false);
    };
    fetchAnalytics();
  }, [supabase]);

  const COLORS = ["#00C49F", "#FFBB28", "#FF8042", "#8884d8", "#FF6666"];

  return (
    <div className="min-h-screen bg-black text-white p-6">
      <div className="max-w-6xl mx-auto">
        <h1 className="text-3xl font-bold mb-8">Analytics Dashboard</h1>

        {loading ? (
          <div className="text-gray-500 text-center mt-20">Loading analytics...</div>
        ) : (
          <motion.div
            initial={{ opacity: 0 }}
            animate={{ opacity: 1 }}
            className="space-y-10"
          >
            <section className="bg-[#0b0b0b] border border-gray-800 rounded-2xl p-6">
              <h2 className="text-xl font-semibold mb-4">ROI Trend</h2>
              <ResponsiveContainer width="100%" height={300}>
                <LineChart data={roiData}>
                  <XAxis dataKey="date" stroke="#666" />
                  <YAxis stroke="#666" />
                  <Tooltip />
                  <Line type="monotone" dataKey="roi" stroke="#00C49F" strokeWidth={2} />
                </LineChart>
              </ResponsiveContainer>
            </section>

            <section className="bg-[#0b0b0b] border border-gray-800 rounded-2xl p-6">
              <h2 className="text-xl font-semibold mb-4">Asset Allocation</h2>
              <ResponsiveContainer width="100%" height={300}>
                <PieChart>
                  <Pie
                    data={allocData}
                    dataKey="value"
                    nameKey="name"
                    outerRadius={100}
                    fill="#8884d8"
                  >
                    {allocData.map((_, index) => (
                      <Cell key={index} fill={COLORS[index % COLORS.length]} />
                    ))}
                  </Pie>
                  <Tooltip />
                </PieChart>
              </ResponsiveContainer>
            </section>
          </motion.div>
        )}
      </div>
    </div>
  );
}
